function urlskip(url, type = '') {
	if(!url){
		return console.log('');
	}
	
	if(!type){
		return console.log(url+'');
	}
	
	if(type == 'navigateTo'){
		return uni.navigateTo({
			url: url
		});
	}
	
	if(type == 'redirectTo'){
		return uni.redirectTo({
			url: url
		});
	}
	
	if(type == 'reLaunch'){
		return uni.reLaunch({
			url: url
		});
	}
	
	if(type == 'switchTab'){
		return uni.reLaunch({
			url: url
		});
	}

}

export default urlskip;