const install = (Vue, vm) => {
	Vue.prototype.$u.http.setConfig({
		baseUrl: 'http://192.168.3.243:2233',
		// trueresponseresponse.data
		// truethis.$u.http.interceptor.response
		// originalData: true, 
		// content-type
		// header: {
		// 	'content-type': 'xxx'
		// }
	});
	// Token
	Vue.prototype.$u.http.interceptor.request = (config) => {
		let sid = "";
		let tugeKey = uni.getStorageSync("TUGE");
		if(null != tugeKey && "" != tugeKey){
			let tuge = JSON.parse(tugeKey);
			sid = tuge.id;
		}
		config.header.Token = "{\"sid\":\""+sid+"\", \"type\": 1}";
		
		// vuextokenuViewvuexhttps://uviewui.com/components/globalVariable.html
		// config.header.token = vm.token;
		
		// uViewvuex$store.state
		// config.header.token = vm.$store.state.token;
		
		// tokenglobalDatagetApp().globalData
		// config.header.token = getApp().globalData.username;
		
		// tokenStorageStorage
		// const token = uni.getStorageSync('token');
		// config.header.token = token;
		
		return config; 
	}
	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		console.log("")
		console.log(res)
		// originalDatatrue
		// res.statueCoderes.data.code
		if(res.code == 200) {
			// originalDatatruereturnthis.$u.postthen
			return res.data;  
		}else if(res.code == 101){
			uni.showToast({
				icon: 'none',
				title: ''
			})
			return false
		}else if(res.code == 102){
			uni.showToast({
				icon: 'none',
				title: ''
			})
			return false
		} else return false;
	}
}

export default {
	install
}